<template>
  <div class="container">
      <h1 class="animate__flash">session debug</h1>
      <p>age : {{nameget()}}</p>
      <button class="btn btn-primary " v-on:click="addAge()">add age</button>
  </div>
</template>

<script>
//let store =require('../../store/data/debug.js')
import store from '../../store/debug/debug.js'
export default {
  store:store,
  data:function (){
    return {
      name:'gedeon'
    }
  },
  methods:{
    nameget(){
      //console.log(this.$store.state.age)
      return this.$store.getters.age
    },
    addAge(){
      let age=this.nameget()
      this.$store.commit('AddAge',age+1)
    }
  }

}
</script>
