import Vuex from 'vuex'

export default new Vuex.Store({
    state:{
        age:20,
        sexe:'M',
    },
    mutations:{
       AddAge:(state,newVal)=>{
           state.age=newVal
       }
    },
    getters:{
        age:state=>state.age
    },
    actions:{
    },
    strict:true,
})